import React, { Component } from 'react';
import '../index.css';
import 'antd-mobile/dist/antd-mobile.css';
import { Button, Flex, WhiteSpace } from 'antd-mobile';
import MenuTabBar from './menuBar';

class Layout extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
          
        };
    }


    render () {
      return <div className="flex-container" >
      <Flex className="title_bar">
        <Flex.Item><img src="http://assert.zujixie.com/image/logo.png" style={{width:"100px", height:"30px"}}/></Flex.Item>
        <Flex.Item></Flex.Item>
        <Flex.Item>pageName:{this.props.pageName}</Flex.Item>
      </Flex>
      <Flex>
        <Flex.Item>{this.props.children}</Flex.Item>
      </Flex>
      <div id="dg" style={{"z-index": "9999", width:"100%", position: "fixed", left: "0px", bottom: "0px"}}>
        <MenuTabBar style={{width:"100%"}} pageName={this.props.pageName} />
        
      </div>
    </div>
    }
}

export default Layout;



